@import '../styles/colors';

svg {
  &.frost-icon {
    fill: currentColor;

    // Frost icon pack default colors
    &-frost {
      &-add { color: $frost-color-white; }
      &-chevron { color: $frost-color-grey-5; }
      &-close-circle { color: $frost-color-blue-1; }
      &-link { color: $frost-color-white; }
      &-search { color: $frost-color-grey-4; }
    }
  }
}



